.navbar{

  display: flex;
  justify-content: space-between;
  align-items: center;
 
  padding: 0.5rem 3.5rem;
}


.navbar .leftside-header {

  display: flex;
  gap: 1rem;
 
  text-align: center;
}

.logo{

  font-size: 1.5rem;
  font-weight: bold;
}

.navbar .leftside-header span{

  font-size: 1.5rem;
  font-weight: bold;
}


.navbar .rightside-header{

  display: flex;
  justify-content: flex-start;

}

.navbar .rightside-header .menu-btn{

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar .rightside-header .menu-btn ul{

  display: flex;
  gap: 2rem;
  text-align: center;
  margin-right: 4rem;
}


.navbar .rightside-header .menu-btn ul li{

  list-style: none;
  cursor: pointer;
}

.navbar .rightside-header .menu-btn ul li:hover{

 color: var(--orange);
}


@media  screen and (max-width:850px) {

  .navbar .rightside-header .menu-btn {

    display: none;
  }


  .navbar{

  
   
    padding: 0.5rem 1.5rem;
  }
  
}