.about{

  display: flex;
  padding: 0.5rem 3.5rem;
height: 77vh;
  margin-top: 6rem;

}

.about-left{

  
 
  position: relative;
  display: flex;
  flex: 1;
  flex-direction: column;
  gap: 2rem;
  
 
  
}


.about-name{

  display: flex;
  flex-direction: column;
}


.about-name>:nth-child(1){

  color: var(--black);
  font-weight: bold;
  font-size: 3rem;
}

.about-name>:nth-child(2){

  color: var(--orange);
  font-size: 3rem;
  font-weight: bold;
}


.about-name>:nth-child(3){

  color: var(--gray);
  font-size: 1rem;
  font-weight: 100;
  margin-top: 0.7rem;

}

.about-button{

  width: 8rem;
}


.about-icons{

  margin-top: 1rem;
  gap: 0rem;
  display: flex;
}

.about-icons>*{
  transform: scale(0.5);
}

.about-icons>*:hover{

  cursor: pointer;
}



.about-right{

  flex: 1;
  position: relative;
}

.about-right>*{

  position: absolute;
  z-index: 1;
}


.about-right>:nth-child(1){
  transform: scale(0.69);
  left: -15%;
  top: -9%;
}

.about-right>:nth-child(2){
  top: -4.6rem;
  transform: scale(0.67);
  left: -3rem;
 
}


.about-right>:nth-child(3){
 
  transform: scale(1.4);
  left: 28%;
 
}



.about-right>:nth-child(4){
 
  transform: scale(0.6);
  left: -24%;
  top: -19%;
  border-radius: 50%; 
}

.blur{

  position: absolute;
  width: 22rem;
  height: 14rem;
  border-radius: 50%;
  background: #edd0ff;
  filter: blur(212px);
  z-index: -9;
  top: -18%;
  left: 56%;
  filter: blur(72px);

}


@media screen and (max-width: 750px){

  .about{

    flex-direction: column;
    gap: 7rem;
    height: 64rem;
  }

  .about-right{

    transform: scale(0.8);
    left: -3rem;
  }

  .about .blur {
    display: none;
}

.floating-div:nth-of-type(1){
   display: none;
  
}

}